<template>
  <div>
    <NavTemplent title="活跃用户"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
<!--      <div>-->
<!--        <h5 style="font-weight: bold">数据筛选</h5>-->
<!--        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">-->
<!--          <el-date-picker-->
<!--            style="width: 250px"-->
<!--            size="mini"-->
<!--            v-model="form.date"-->
<!--            type="daterange"-->
<!--            format="yyyy/MM/dd"-->
<!--            start-placeholder="开始日期"-->
<!--            end-placeholder="结束日期">-->
<!--          </el-date-picker>-->
<!--          <el-form-item style="text-align: right">-->
<!--            <el-button type="primary" @click="getActiveUser">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
<!--        </el-form>-->
<!--      </div>-->
      <section id="auth-button"></section>
      <section id="view-selector"></section>
<!--      <el-radio-group v-model="form.activeDay" style="margin-top: 20px" @change="getActiveUser">-->
<!--        <el-radio label="1天活跃用户数"></el-radio>-->
<!--        <el-radio label="7天活跃用户数"></el-radio>-->
<!--        <el-radio label="14天活跃用户数"></el-radio>-->
<!--        <el-radio label="28天活跃用户数"></el-radio>-->
<!--      </el-radio-group>-->
      <el-row>
        <el-col :span="12">
          <section id="timeline"></section>
        </el-col>
        <el-col :span="12">
          <section id="timeline1"></section>
        </el-col>
        <el-col :span="12">
          <section id="timeline2"></section>
        </el-col>
        <el-col :span="12">
          <section id="timeline3"></section>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    export default {
        components: {
            NavTemplent
        },
        data() {
            return {
                form: {
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    activeDay: '1天活跃用户数'
                }
            }
        },
        name: "index",
        methods: {
            getActiveUser() {
                // Step 5: Create the timeline chart.
                gapi.analytics.ready(function() {
                        var aa = '222';
                        console.log(aa);
                        var CLIENT_ID = '628874914398-uqthhdqsb0sv06s1sork5j1uf4k69555.apps.googleusercontent.com';

                        gapi.analytics.auth.authorize({
                            container: 'auth-button',
                            clientid: CLIENT_ID,
                        });

                        // Step 4: Create the view selector.

                        var viewSelector = new gapi.analytics.ViewSelector({
                            container: 'view-selector'
                        });

                        var timeline = new gapi.analytics.googleCharts.DataChart({
                            reportType: 'ga',
                            query: {
                                'dimensions': 'ga:day',
                                'metrics': 'ga:1dayUsers',
                                'start-date': '30daysAgo',
                                'end-date': 'yesterday',
                            },
                            chart: {
                                type: 'LINE',
                                container: 'timeline'
                            }
                        });

                        var timeline1 = new gapi.analytics.googleCharts.DataChart({
                            reportType: 'ga',
                            query: {
                                'dimensions': 'ga:day',
                                'metrics': 'ga:7dayUsers',
                                'start-date': '30daysAgo',
                                'end-date': 'yesterday',
                            },
                            chart: {
                                type: 'LINE',
                                container: 'timeline1'
                            }
                        });
                        var timeline2 = new gapi.analytics.googleCharts.DataChart({
                        reportType: 'ga',
                        query: {
                            'dimensions': 'ga:day',
                            'metrics': 'ga:14dayUsers',
                            'start-date': '30daysAgo',
                            'end-date': 'yesterday',
                        },
                        chart: {
                            type: 'LINE',
                            container: 'timeline2'
                        }
                    });
                        var timeline3 = new gapi.analytics.googleCharts.DataChart({
                        reportType: 'ga',
                        query: {
                            'dimensions': 'ga:day',
                            'metrics': 'ga:28dayUsers',
                            'start-date': '30daysAgo',
                            'end-date': 'yesterday',
                        },
                        chart: {
                            type: 'LINE',
                            container: 'timeline3'
                        }
                    });

                        gapi.analytics.auth.on('success', function(response) {
                            viewSelector.execute();
                        });

                        viewSelector.on('change', function(ids) {
                            var newIds = {
                                query: {
                                    ids: ids
                                }
                            };
                            timeline.set(newIds).execute();
                            timeline1.set(newIds).execute();
                            timeline2.set(newIds).execute();
                            timeline3.set(newIds).execute();
                        });

                    });
            }
        },
        mounted() {
            this.$nextTick(() => {
                this.getActiveUser()
            })
        }
    }
</script>

<style scoped>
  /deep/ #view-selector table{
    margin-top: 20px;
    width: 200px;
    display: inline-block;
  }
  /deep/ #view-selector table .shr-a-shr-fc-shr-fb-shr-cb-shr-pe{
    width: 200px;
  }
</style>




