<template>
  <div>
    <NavTemplent title="客服部门指标项"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getDepartmentList"
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="姓名" v-if="!isDepartent">
            <el-input v-model="form.userName" placeholder="请输入姓名" @keyup.enter.native="getDepartmentList"></el-input>
          </el-form-item>
<!--          <el-form-item style="text-align: right">-->
<!--            <el-button type="primary" @click="getDepartmentList">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <div v-if="!isDepartent">
        <div class="tableTitle">
        <span>
          <div>指标列表</div>
        </span>
        </div>
        <el-table
          :data="userList"
          border
          style="width: 100%">
          <el-table-column
            type="index">
          </el-table-column>
          <el-table-column
            label="员工姓名">
            <template slot-scope="scope">
              <el-link @click="openDetails(scope.row)" style="color:#409EFF">{{scope.row.name || '--'}}</el-link>
            </template>
          </el-table-column>
          <el-table-column
            v-for="item in quotaList"
            :key="item.quota_id"
            :label="item.quota_name">
            <template slot-scope="scope">
              <el-link>--</el-link>
            </template>
          </el-table-column>
        </el-table>
        <TablePage :pageSize="page.pageNum" :pageNum="page.pageSize" :total="total" @update:pageSize="updateSize" @update:pageNum="updateNum"></TablePage>
      </div>
      <div v-if="isDepartent">
        <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div>数据列表</div>
        </span>
          <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="editquatoMessage"><i class="el-icon-plus"></i>添加</el-button>
        </span>
        </div>
        <el-table
          :data="userQuotaList"
          border
          style="width: 100%">
          <el-table-column
            type="index">
          </el-table-column>
          <el-table-column
            label="指标名称">
            <template slot-scope="scope">
              <el-link>{{scope.row.quota_name || '--'}}</el-link>
            </template>
          </el-table-column>
          <el-table-column
            label="目标值">
            <template slot-scope="scope">
              <span>{{scope.row.target_value || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="实际完成值">
            <template slot-scope="scope">
              <span>{{scope.row.complete_value || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="得分项">
            <template slot-scope="scope">
              <span>{{scope.row.score || '--'}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="时间">
            <template slot-scope="scope">
              <span>{{ scope.row.add_time | dateFormat('YYYY/MM/DD')}}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-link @click="editquatoMessage(scope.row)" style="color:#409EFF">编辑</el-link>
              <el-link @click="delquatoMessage(scope.row)" style="color:#409EFF">删除</el-link>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px" append-to-body>
        <el-form size="mini">
          <el-form-item label="姓名">
            <el-select v-model="form.user_id" placeholder="请选择姓名">
              <el-option v-for="item in userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="指标">
            <el-select v-model="form.quota_id" placeholder="请选择指标">
              <el-option v-for="item in quotaList" :key="item.quota_id" :label="item.quota_name" :value="item.quota_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标值">
            <el-input v-model="form.target_value" placeholder="请输入目标值"></el-input>
          </el-form-item>
          <el-form-item label="单位">
            <el-select v-model="form.unit" placeholder="请选择单位">
              <el-option label="百分比" :value="1"></el-option>
              <el-option label="个数" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标期类型">
            <el-select v-model="form.target_date_type" placeholder="请选择目标类型">
              <el-option label="日" :value="1"></el-option>
              <el-option label="周" :value="2"></el-option>
              <el-option label="月" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="实际完成值">
            <el-input v-model="form.complete_value" placeholder="请输入实际完成值"></el-input>
          </el-form-item>
          <el-form-item label="该项得分">
            <el-input v-model="form.score" placeholder="请输入得分"></el-input>
          </el-form-item>
          <el-form-item label="选择日期">
            <el-date-picker
              v-model="form.complete_date"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="form.remark" placeholder="请输入备注"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                userQuotaList: [],
                quotaList: [],
                editUserMessage: null,
                departmentList: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                editUse: null,
                title: '用户信息',
                isDepartent: false,
                dialogVisible: false,
                userList: [],
                total: 0,
                userId: '',
                form: {
                    userName: '',
                    complete_date: '',
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    departmentId: '',
                    user_id: '',
                    quota_id: '',
                    target_value: '',
                    unit: '',
                    target_date_type: '',
                    complete_value: '',
                    score: '',
                    remark: ''
                }
            }
        },
        methods: {
            getDepartmentList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list.filter(item => {
                        return item.department_name === '客服部'
                    });
                    this.$post('getQuotaList', {
                        pageNum: this.page.pageNum,
                        page: this.page.pageSize,
                        department_id: this.departmentList[0].department_id
                    }).then(resQuotaList => {
                        this.quotaList = resQuotaList.list
                    });
                    if (this.isDepartent) {
                        this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                        this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                        this.$post('getUserQuotaList', {
                            pageNum: this.page.pageNum,
                            page: this.page.pageSize,
                            department_id: this.departmentList[0].department_id,
                            user_id: this.userId,
                            start_time: this.startTime,
                            end_time: this.lastTime,
                        }).then(resQuotaList => {
                            this.userQuotaList = resQuotaList.list
                        })
                    }else {
                        this.$post('/getUserList', {
                            pageNum: this.page.pageNum,
                            page: this.page.pageSize,
                            department_id: this.departmentList[0].department_id
                        }).then(resUserList => {
                            this.total = resUserList.count;
                            this.userList = resUserList.list
                        });
                    }
                }).catch(err => {
                    this.$message.error(err)
                });
            },
            updateSize(val) {
                this.page.pageSize = 1;
                this.page.pageNum = val;
                this.getDepartmentList();
            },
            updateNum(val) {
                this.page.pageSize = val;
                this.getDepartmentList();
            },
            openDetails(data) {
                this.userId = data.id;
                this.isDepartent = true;
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('getUserQuotaList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    department_id: this.departmentList[0].department_id,
                    user_id: data.id,
                    start_time: this.startTime,
                    end_time: this.lastTime,
                }).then(resQuotaList => {
                    this.userQuotaList = resQuotaList.list
                })
            },
            editquatoMessage(data) {
                this.editUserMessage = data;
                this.dialogVisible = true;
                if (data.user_quota_id) {
                    this.form = data;
                    this.form.complete_date = data.complete_date * 1000;
                }else {
                    this.form.user_id = '';
                    this.form.quota_id = '';
                    this.form.target_value = '';
                    this.form.unit = '';
                    this.form.target_date_type = '';
                    this.form.complete_value = '';
                    this.form.score = '';
                    this.form.complete_date = '';
                    this.form.remark = ''
                }
            },
            delquatoMessage(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delUserQuota', {
                        user_quota_id: data.user_quota_id
                    }).then(() => {
                        this.getDepartmentList();
                        this.$message.success('指标删除成功')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editUserMessage.user_quota_id) {
                    this.$post('editUserQuota', {
                        user_quota_id: this.editUserMessage.user_quota_id,
                        user_id: this.form.user_id,
                        department_id: this.departmentList[0].department_id,
                        target_value: this.form.target_value,
                        target_date_type: this.form.target_date_type,
                        complete_value: this.form.complete_value,
                        score: this.form.score,
                        complete_date: moment(this.form.complete_date).format('YYYY/MM/DD'),
                        remark: this.form.remark,
                        unit: this.form.unit,
                        quota_id: this.form.quota_id
                    }).then(res => {
                        this.$message.success('用户指标修改成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getDepartmentList()
                    })
                }else {
                    this.$post('addUserQuota', {
                        user_id: this.form.user_id,
                        department_id: this.departmentList[0].department_id,
                        target_value: this.form.target_value,
                        target_date_type: this.form.target_date_type,
                        complete_value: this.form.complete_value,
                        score: this.form.score,
                        complete_date: this.form.complete_date,
                        remark: this.form.remark,
                        unit: this.form.unit,
                        quota_id: this.form.quota_id
                    }).then(res => {
                        this.$message.success('用户新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getDepartmentList();
                    })
                }
            }
        },
        mounted() {
            this.getDepartmentList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select, .el-date-editor{
    width: 100%;
  }
</style>



