<template>
  <div>
    <NavTemplent title="自定义"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <section id="auth-button"></section>
      <section id="view-selector"></section>
      <el-select label="指标选择" v-model="choose">
        <el-option label="查看详情后购买的比例" value="0"></el-option>
        <el-option label="查看详情后添加到购物车的比例" value="1"></el-option>
        <el-option label="产品被添加到购物车的次数" value="2"></el-option>
        <el-option label="产品收入" value="3"></el-option>
        <el-option label="产品详情视图" value="4"></el-option>
        <el-option label="购买的产品数量" value="5"></el-option>
        <el-option label="唯一身份购买次数" value="6"></el-option>
      </el-select>
      <el-tabs v-model="activeName">
        <el-tab-pane label="单品报告" name="first">
          <el-row>
            <el-col :span="24">
              <section id="timelines"></section>
            </el-col>
            <el-col :span="24">
              <section id="timeline"></section>
            </el-col>
          </el-row>
        </el-tab-pane>
<!--        <el-tab-pane label="品类报告" name="second">品类报告</el-tab-pane>-->
      </el-tabs>
    </div>
  </div>
</template>
<script>
    import NavTemplent from "../../../components/NavTemplent";
    export default {
        data() {
            return {
                activeName: 'first',
                choose: '0'
            }
        },
        components: {
            NavTemplent
        },
        name: "index",
        methods: {
            getInitialData() {
                this.$nextTick(() => {
                    gapi.analytics.ready(function() {
                        var CLIENT_ID = '628874914398-uqthhdqsb0sv06s1sork5j1uf4k69555.apps.googleusercontent.com';

                        gapi.analytics.auth.authorize({
                            container: 'auth-button',
                            clientid: CLIENT_ID,
                        });

                        // Step 4: Create the view selector.

                        var viewSelector = new gapi.analytics.ViewSelector({
                            container: 'view-selector'
                        });

                        // Step 5: Create the timeline chart.

                        var timeline = new gapi.analytics.googleCharts.DataChart({
                            reportType: 'ga',
                            query: {
                                'dimensions': 'ga:productSku,ga:shoppingStage,ga:productName',
                                'metrics': 'ga:productDetailViews,ga:itemQuantity,ga:cartToDetailRate',
                                'start-date': '30daysAgo',
                                'end-date': 'yesterday',
                                // 'start-index': 10,
                                // 'max-results': 100,
                                // 'totalResults': 10,
                                // 'itemsPerPage': 20
                            },
                            chart: {
                                type: 'TABLE',
                                container: 'timeline'
                            }
                        });

                        // Step 6: Hook up the components to work together.

                        var timelines = new gapi.analytics.googleCharts.DataChart({
                            reportType: 'ga',
                            query: {
                                'dimensions': 'ga:fullReferrer',
                                'metrics': 'ga:localItemRevenue',
                                'start-date': '30daysAgo',
                                'end-date': 'yesterday',
                            },
                            chart: {
                                type: 'LINE',
                                container: 'timelines'
                            }
                        });

                        // Step 6: Hook up the components to work together.

                        viewSelector.on('change', function(ids) {
                            var newIds = {
                                query: {
                                    ids: ids
                                }
                            };
                            timeline.set(newIds).execute();
                            timelines.set(newIds).execute();
                        });

                        gapi.analytics.auth.on('success', function(response) {
                            viewSelector.execute();
                        });
                    });
                });
            }
        },
        mounted() {
            this.getInitialData();
            this.$post('https://analytics.google.com/analytics/web/getPage?233-graphOptions.primaryConcept=analytics.cartToDetailRate&233-table-tableMode.selected=data&id=ZtBbCdE2RnKm76wa5F1gzQ&ds=a137360274w197834198p192592496&rn=customReport&cid=233-graph%2C233-graphOptions%2CtimestampMessage&hl=zh_CN&authuser=0', {
                token: 'AO6Y7m8vhTCeP19ntTt4eVGJP33STjNNFw:1577333035295'
            }).then(res => {
                console.log(res)
            })
        }
    }
</script>

<style scoped>
  /deep/ #view-selector table{
    margin-top: 20px;
    width: 200px;
    display: inline-block;
  }
  /deep/ #view-selector table .shr-a-shr-fc-shr-fb-shr-cb-shr-pe{
    width: 200px;
  }
</style>



