<template>
  <div>
    <NavTemplent title="物流费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-form-item label="站点">
            <el-select v-model="form.web_id" placeholder="请选择站点" @change="updateSite">
              <el-option v-for="item in siteList" :key="item.web_id" :value="item.web_id" :label="item.web_name"></el-option>
            </el-select>
          </el-form-item>
<!--          <el-form-item label="状态">-->
<!--            <el-select v-model="form.status" placeholder="请选择状态">-->
<!--              <el-option label="审核通过" :value="0"></el-option>-->
<!--              <el-option label="待通过" :value="1"></el-option>-->
<!--              <el-option label="审核未通过" :value="2"></el-option>-->
<!--            </el-select>-->
<!--          </el-form-item>-->
<!--          <el-form-item>-->
<!--            <el-button type="primary" @click="getLogisticsList">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <el-row>
          <el-col :span="24">
            <div id="myChart" style="width: 100%;height: 400px"></div>
          </el-col>
      </el-row>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div style="float: left;width: 100px">物流列表</div>
          <el-form style="float: left" label-width="80px" inline @submit.native.prevent size="mini">
            <el-date-picker
              @change="getLogisticsList"
              style="width: 250px"
              size="mini"
              v-model="form.date"
              type="daterange"
              format="yyyy/MM/dd"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="downLoad"><i class="el-icon-download"></i>导出</el-button>
<!--          <el-button size="mini" type="primary" @click="editLogisticsMessage"><i class="el-icon-plus"></i>新增</el-button>-->
        </span>
      </div>
      <el-table
        :data="logisticsList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="仓库单号">
          <template slot-scope="scope">
            <span>{{scope.row.warehouseOrderCode || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="物流单号">
          <template slot-scope="scope">
            <span>{{scope.row.shippingMethodNo || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="重量">
          <template slot-scope="scope">
            <span>{{scope.row.orderWeight || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="运输渠道">
          <template slot-scope="scope">
            <span>{{scope.row.shippingMethod}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="单价">
          <template slot-scope="scope">
            <span>{{ scope.row.shipFee}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="发货日期">
          <template slot-scope="scope">
            <span>{{ scope.row.dateWarehouseShipping * 1000 | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="总费用">
          <template slot-scope="scope">
            <span>{{ scope.row.platformFeeTotal}}</span>
          </template>
        </el-table-column>
<!--        <el-table-column-->
<!--          label="操作">-->
<!--          <template slot-scope="scope">-->
<!--            <el-link @click="editLogisticsMessage(scope.row)" style="color:#409EFF">编辑</el-link>-->
<!--            <el-link @click="delLogisticsMessage(scope.row)" style="color:#409EFF">删除</el-link>-->
<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>
      <TablePage :pageSize="page.pageNum" :pageNum="page.pageSize" :total="total" @update:pageSize="updateSize" @update:pageNum="updateNum"></TablePage>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="750px">
      <el-form size="mini">
        <el-form-item label="单号">
          <el-input v-model="form.warehouseOrderCode" placeholder="请输入单号"></el-input>
        </el-form-item>
        <el-form-item label="跟踪号">
          <el-input v-model="form.shippingMethodNo" placeholder="请输入跟踪号"></el-input>
        </el-form-item>
        <el-form-item label="网站">
          <el-select v-model="form.webId" placeholder="请选择站点">
            <el-option v-for="item in siteList" :key="item.web_id" :value="item.web_id" :label="item.web_name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="运输渠道">
          <el-select v-model="form.shippingMethod" placeholder="请选择运输渠道">
            <el-option v-for="item in shippingList" :key="item.id" :label="item.sm_name_cn" :value="item.sm_code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="重量">
          <el-input v-model="form.orderWeight" placeholder="请输入重量"></el-input>
        </el-form-item>
        <el-form-item label="单价">
          <el-input v-model="form.shipFee" placeholder="请输入单价"></el-input>
        </el-form-item>
        <el-form-item label="处理费">
          <el-input v-model="form.platformFeeTotal" placeholder="请输入处理费"></el-input>
        </el-form-item>
        <el-form-item label="发货日期">
          <el-date-picker
            v-model="form.dateWarehouseShipping"
            type="date"
            placeholder="日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="总费用">
          <span>￥{{form.total_fee || 0}}</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
        <el-button size="mini" type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                title: '编辑物流费用',
                logisticsList: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                editLogisticMessage: null,
                dialogVisible: false,
                valueList: [],
                dateList: [],
                siteList: null,
                roleList: [],
                total: 0,
                shippingList: [],
                form: {
                    web_id: '',
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    status: 0,
                    total_fee: '',
                    dateWarehouseShipping: '',
                    platformFeeTotal: '',
                    shipFee: '',
                    orderWeight: '',
                    shippingMethod: '',
                    shippingMethodNo: '',
                    warehouseOrderCode: '',
                    webId: ''
                }
            }
        },
        methods: {
            updateSite() {
                this.getLogisticsList();
                this.getLogisticsLineChart()
            },
            downLoad() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$exportExcel('/getLogisticsList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    lastTime: this.lastTime,
                    startTime: this.startTime,
                    web_id: this.form.web_id,
                    download: 1,
                }).then(() => {
                    this.$message.success('下载成功')
                })
            },
            updateSize(val) {
                this.page.pageSize = 1;
                this.page.pageNum = val;
                this.getLogisticsList();
            },
            updateNum(val) {
                this.page.pageSize = val;
                this.getLogisticsList();
            },
            getLogisticsList() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getLogisticsList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    lastTime: this.lastTime,
                    startTime: this.startTime,
                    web_id: this.form.web_id
                }).then(res => {
                    this.logisticsList = res.list
                })
            },
            getSiteList() {
                this.$post('getGaWebSitList', {
                    pageNum: 10000,
                    page: this.page.pageSize
                }).then(res => {
                    this.siteList = res.list
                })
            },
            getLogisticsLineChart() {
                console.log(this.form);
                this.$post('getLogisticsLineChart', {
                    web_id: this.form.web_id
                }).then(res => {
                    this.dateList = res.map(item => {
                        return item.date
                    });
                    this.valueList = res.map(item => {
                        return item.value
                    });
                    if (res) {
                        this.drawLine();
                    }
                })
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '物流费用'},
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.dateList
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.valueList,
                        type: 'line',
                        areaStyle: {}
                    }]
                });
            },
            editLogisticsMessage(data) {
                this.editLogisticMessage = data;
                this.getShippingMethod();
                this.dialogVisible = true;
                if (data.id) {
                    this.form = data;
                    this.form.dateWarehouseShipping = data.dateWarehouseShipping * 1000
                }else {
                    this.form.package_num = '';
                    this.form.package_price = '';
                    this.form.zipper_package_num = '';
                    this.form.zipper_package_price = '';
                    this.form.ticket_price = '';
                    this.form.ticket_num = '';
                    this.form.buy_time = '';
                    this.form.img_path = ''
                }
            },
            delLogisticsMessage(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delLogistics', {
                        id: data.id
                    }).then(() => {
                        this.$message.success('物料删除成功');
                        this.getLogisticsList();
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            getShippingMethod() {
                this.$post('getShippingMethod').then(res => {
                    this.shippingList = res
                })
            },
            save() {
                if (this.editLogisticMessage.id) {
                    this.$post('editLogistics', {
                        id: this.editLogisticMessage.id,
                        warehouseOrderCode: this.form.warehouseOrderCode,
                        shippingMethodNo: this.form.shippingMethodNo,
                        orderWeight: this.form.orderWeight,
                        shippingMethod: this.form.shippingMethod,
                        platformFeeTotal: this.form.platformFeeTotal,
                        shipFee: this.form.shipFee,
                        dateWarehouseShipping: moment(this.form.dateWarehouseShipping).format('YYYY/MM/DD'),
                        webId: this.form.webId
                    }).then(() => {
                        this.$message.success('物料信息修改成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getLogisticsList()
                    })
                }else {
                    this.$post('addLogistics', {
                        warehouseOrderCode: this.form.warehouseOrderCode,
                        shippingMethodNo: this.form.shippingMethodNo,
                        orderWeight: this.form.orderWeight,
                        shippingMethod: this.form.shippingMethod,
                        platformFeeTotal: this.form.platformFeeTotal,
                        shipFee: this.form.shipFee,
                        dateWarehouseShipping: this.form.dateWarehouseShipping,
                        webId: this.form.webId
                    }).then(res => {
                        this.$message.success('物料新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getLogisticsList()
                    })
                }
            }
        },
        mounted() {
            this.getLogisticsList();
            this.getSiteList();
            this.getLogisticsLineChart();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select, .date{
    width: 100%;
  }
</style>



