<template>
  <div>
    <NavTemplent title="物料费用"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getMaterialList"
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
<!--          <el-form-item>-->
<!--            <el-button type="primary">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <el-row>
        <el-col :span="24">
          <div id="myChart" style="width: 100%;height: 400px"></div>
        </el-col>
      </el-row>
      <div class="tableTitle">
        <span style="width: 50%;float: left">
          <div>物料列表</div>
        </span>
        <span style="width: 50%;float: right;text-align: right">
          <el-button size="mini" type="primary" @click="downLoad"><i class="el-icon-download"></i>导出</el-button>
          <el-button size="mini" type="primary" @click="editMaterialsMessage"><i class="el-icon-plus"></i>新增</el-button>
        </span>
      </div>
      <el-table
        :data="materialList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="快递袋">
          <template slot-scope="scope">
            <span>￥{{scope.row.package_price || '--'}} （{{scope.row.package_num}}个)</span>
          </template>
        </el-table-column>
        <el-table-column
          label="包装拉链袋">
          <template slot-scope="scope">
            <span>￥{{scope.row.zipper_package_price || '--'}} （{{scope.row.zipper_package_num}}个)</span>
          </template>
        </el-table-column>
        <el-table-column
          label="热敏打印机">
          <template slot-scope="scope">
            <span>￥{{scope.row.ticket_num || '--'}} （{{scope.row.ticket_price}}个)</span>
          </template>
        </el-table-column>
        <el-table-column
          label="总费用">
          <template slot-scope="scope">
            <span>{{scope.row.total_price || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 0">待审核</span>
            <span v-else-if="scope.row.status === 1">通过</span>
            <span v-else>不通过</span>
          </template>
        </el-table-column>
        <el-table-column
          label="购买日期">
          <template slot-scope="scope">
            <span>{{ scope.row.created_at | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <el-link @click="editMaterialsMessage(scope.row)" style="color:#409EFF">编辑</el-link>
            <el-link @click="delMaterialMessage(scope.row)" style="color:#409EFF">删除</el-link>
          </template>
        </el-table-column>
      </el-table>
      <TablePage :pageSize="page.pageNum" :pageNum="page.pageSize" :total="total" @update:pageSize="updateSize" @update:pageNum="updateNum"></TablePage>
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="750px">
        <el-form size="mini">
          <el-form-item label="快递袋">
            <el-input style="width: 40%" v-model="form.package_num" placeholder="请输入个数（例如：50个）"></el-input>
            <el-input style="width: 40%" v-model="form.package_price" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="包装拉链袋">
            <el-input style="width: 40%" v-model="form.zipper_package_num" placeholder="请输入个数（例如：50个）"></el-input>
            <el-input style="width: 40%" v-model="form.zipper_package_price" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="热敏打印机">
            <el-input style="width: 40%" v-model="form.ticket_num" placeholder="请输入个数（例如：50个）"></el-input>
            <el-input style="width: 40%" v-model="form.ticket_price" placeholder="请输入单价"></el-input>
          </el-form-item>
          <el-form-item label="站点">
            <el-select v-model="form.web_id" placeholder="请选择站点">
              <el-option v-for="item in siteList" :key="item.web_id" :value="item.web_id" :label="item.web_name"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="购买日期">
            <el-date-picker
              v-model="form.buy_time"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="总费用">
            <span>￥{{form.total_price || 0}}</span>
          </el-form-item>
          <el-form-item label="上传图片">
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="form.img_path" :src="form.img_path" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="mini" type="danger" @click="cancle">取 消</el-button>
          <el-button size="mini" type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                siteList: [],
                imageUrl: '',
                title: '编辑物料费用',
                date: '',
                editMaterialMessage: null,
                materialList: [],
                buyTimeList: [],
                totalNums: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                dialogVisible: false,
                total: 0,
                isDownload: '0',
                form: {
                    web_id: '',
                    buy_time: '',
                    package_num: '',
                    package_price: '',
                    zipper_package_num: '',
                    zipper_package_price: '',
                    ticket_num: '',
                    ticket_price: '',
                    total_price: '',
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    img_path: ''
                }
            }
        },
        methods: {
            // 站点列表
            getSiteList() {
                this.$post('/getGaWebSitList', {
                    pageNum: 10000,
                    page: this.page.pageSize,
                    website_name: this.form.siteName
                }).then(res => {
                    this.siteList = res.list
                })
            },
            handleAvatarSuccess(res, file) {
                this.form.img_path = URL.createObjectURL(file.raw);
            },
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '物料费用'},
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.buyTimeList
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [820, 932, 1330, 901, 1290, 934, 1320],
                        type: 'line',
                        areaStyle: {}
                    }]
                });
            },
            getMaterialList() {
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$post('/getMaterialList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    lastTime: this.lastTime,
                    startTime: this.startTime,
                    download: this.isDownload,
                }).then(res => {
                    this.total = res.count;
                    this.materialList = res.list;
                    this.buyTimeList = res.list.map(item => {
                        return moment(item.buy_time * 1000).format('YYYY/MM/DD')
                    });
                    this.totalNums = res.list.map(item => {
                        return item.total_price
                    });
                    if (this.buyTimeList.length && this.totalNums.length) {
                        this.drawLine()
                    }
                })
            },
            updateSize(val) {
                this.page.pageSize = 1;
                this.page.pageNum = val;
                this.getMaterialList();
            },
            updateNum(val) {
                this.page.pageSize = val;
                this.getMaterialList();
            },
            downLoad() {
                this.isDownload = 1;
                this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                this.$exportExcel('/getMaterialList', {
                    pageNum: this.page.pageNum,
                    page: this.page.pageSize,
                    lastTime: this.lastTime,
                    startTime: this.startTime,
                    download: this.isDownload,
                }).then(() => {
                    this.$message.success('下载成功')
                })
            },
            editMaterialsMessage(data) {
                this.editMaterialMessage = data;
                this.dialogVisible = true;
                if (data.material_cost_id) {
                    this.form = data;
                    this.form.buy_time = data.buy_time * 1000
                }else {
                    this.form.package_num = '';
                    this.form.package_price = '';
                    this.form.zipper_package_num = '';
                    this.form.zipper_package_price = '';
                    this.form.ticket_price = '';
                    this.form.ticket_num = '';
                    this.form.buy_time = '';
                    this.form.img_path = ''
                }
            },
            delMaterialMessage(data) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('delMaterial', {
                        material_cost_id: data.material_cost_id
                    }).then(() => {
                        this.$message.success('物料删除成功');
                        this.getMaterialList();
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            cancle() {
                this.dialogVisible = false;
                this.$emit('close')
            },
            save() {
                if (this.editMaterialMessage.material_cost_id) {
                    this.$post('editMaterial', {
                        material_cost_id: this.editMaterialMessage.material_cost_id,
                        package_num: this.form.package_num,
                        package_price: this.form.package_price,
                        zipper_package_num: this.form.zipper_package_num,
                        zipper_package_price: this.form.zipper_package_price,
                        ticket_price: this.form.ticket_price,
                        ticket_num: this.form.ticket_num,
                        buy_time: moment(this.form.buy_time).format('YYYY/MM/DD'),
                        image: this.form.img_path
                    }).then(() => {
                        this.$message.success('物料信息修改成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getMaterialList()
                    })
                } else {
                    this.$post('addMaterial', this.form).then(res => {
                        this.$message.success('物料新增成功');
                        this.dialogVisible = false;
                        this.$emit('close');
                        this.getMaterialList()
                    })
                }
            }
        },
        mounted() {
            this.getSiteList();
            this.getMaterialList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select, .date{
    width: 100%;
  }
  /deep/ .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



